import React, { useEffect } from 'react';
import './App.css';
import { CurrentForm, CurrentWordList, MetricsContainer } from 'containers';
import { saveState, store } from 'store';

function App() {
  useEffect(
    () =>
      store.subscribe(() => {
        const state = store.getState();
        Object.keys(state).forEach((key) => {
          const k = key as keyof typeof state;
          saveState(k, state[k]);
        });
      }),
    []
  );
  return (
    <div className="App bg-gradient-to-r from-pink-400 via-purple-500 to-indigo-600 min-h-screen min-w-full">
      <div className="flex flex-row">
        <div className="w-full lg:w-1/3 p-4">
          {/* <DarkModeToggle /> */}
          <CurrentForm />
        </div>
        <div className="w-full lg:w-1/3 p-4 overflow-y-auto h-100">
          <CurrentWordList />
        </div>
        <div className="w-full lg:w-1/3 p-4">
          <MetricsContainer />
        </div>
      </div>
      <div className="fixed bottom-4 right-4 bg-opacity-50 bg-white text-right py-2 px-4 rounded-full">
        Made with <span className="text-red-500">❤️</span> by{' '}
        <a
          href="https://github.com/schie"
          target="_blank"
          rel="noopener noreferrer"
          className="text-blue-500 hover:underline"
        >
          @schie
        </a>
      </div>
    </div>
  );
}

export default App;
