import React, { FC, useCallback, useEffect, useMemo } from 'react';

import { WordListItem as WLI, WordListState, WordScore } from '_types';

interface WordListItemScoreButtonProps {
  score: WordScore;
  value: WordScore;
  onClick: (score: WordScore) => void;
}
const WordListItemScoreButton: FC<WordListItemScoreButtonProps> = ({
  score,
  value,
  onClick,
}) => {
  const matches = useMemo(() => score === value, [score, value]);

  const buttonClass = useMemo(() => {
    const styles = ['px-2 py-1 text-xs rounded-full'];
    switch (score) {
      case WordScore.Correct:
        styles.push('hover:bg-green-200');
        break;
      case WordScore.Incorrect:
        styles.push('hover:bg-red-200');
        break;
      case WordScore.Skipped:
      default:
        styles.push('hover:bg-blue-200');
    }
    if (matches) {
      switch (score) {
        case WordScore.Correct:
          styles.push('bg-green-500 text-white');
          break;
        case WordScore.Incorrect:
          styles.push('bg-red-500 text-white');
          break;
        case WordScore.Skipped:
        default:
          styles.push('bg-blue-500 text-white');
      }
    } else {
      styles.push('bg-gray-100 text-gray-600');
    }

    return styles.join(' ');
  }, [matches, score]);

  const iconClass = useMemo(() => {
    switch (score) {
      case WordScore.Correct:
        return 'fas fa-check';
      case WordScore.Incorrect:
        return 'fas fa-times';
      case WordScore.Skipped:
      default:
        return 'fas fa-step-forward';
    }
  }, [score]);

  const onPressCallback = useCallback(() => onClick(score), [onClick, score]);

  return (
    <button key={score} className={buttonClass} onClick={onPressCallback}>
      <i className={iconClass} />
    </button>
  );
};

interface WordListItemProps extends WLI {
  index: number;
  onScoreChange: (index: number, score: WordScore) => void;
}
const WordListItem: FC<WordListItemProps> = ({
  score,
  notes,
  sounds,
  spellingIndexes,
  index,
  onScoreChange,
}) => {
  const spelling = useMemo(
    () => spellingIndexes.map((i, idx) => sounds[idx]?.letters?.[i]),
    [spellingIndexes, sounds]
  );

  const soundString = useMemo(() => {
    return sounds
      .filter((x) => x)
      .map((s) => s.sound)
      .join(', ');
  }, [sounds]);

  const onScoreButtonClick = useCallback(
    (score: WordScore) => onScoreChange(index, score),
    [index, onScoreChange]
  );

  return (
    <li className="mb-4 border-b border-gray-300">
      <div className="flex flex-col justify-between items-start">
        <p className="text-xl font-semibold mb-2">{spelling}</p>{' '}
        <div className="flex justify-between items-center w-full">
          <div>
            <p className="text-xs text-gray-500">{soundString}</p>
          </div>
          <div className="flex space-x-2">
            {Object.values(WordScore)
              .filter((score) => score !== WordScore.NotScored)
              .map((option, idx) => (
                <WordListItemScoreButton
                  key={`${score}-${idx}`}
                  score={option}
                  value={score}
                  onClick={onScoreButtonClick}
                />
              ))}
          </div>
        </div>
      </div>
      <p className="text-xs italic text-gray-400">{notes}</p>
    </li>
  );
};

interface WordListProps extends WordListState {
  onScoreChange: (index: number, score: WordScore) => void;
}

export const WordList: FC<WordListProps> = ({
  notes,
  words,
  onScoreChange,
}) => {
  return (
    <div>
      <h2 className="text-lg font-semibold mb-4">Word List</h2>
      <p className="text-sm text-gray-600 mb-4">{notes}</p>
      <ul className="bg-white shadow rounded-lg p-4">
        {words.map((word, index) => {
          return (
            <WordListItem
              key={index}
              {...word}
              index={index}
              onScoreChange={onScoreChange}
            />
          );
        })}
      </ul>
    </div>
  );
};
