import { Score, Sound, SoundType } from '_types';
import React, { FC, useEffect, useMemo, useState } from 'react';

interface Props extends Score {
  title: string;
}

export const PercentageMetric: FC<Props> = ({
  percentage,
  fraction,
  title,
}) => {
  const targetPercentage = useMemo(() => percentage * 100, [percentage]);
  const [displayedPercentange, setDisplayedPercentage] = useState(
    percentage * 100
  );

  useEffect(() => {
    const step = targetPercentage > displayedPercentange ? 1 : -1;
    const interval = setInterval(() => {
      setDisplayedPercentage((prev) => {
        const next = prev + step;
        if (
          (step > 0 && next >= targetPercentage) ||
          (step < 0 && next <= targetPercentage)
        ) {
          clearInterval(interval);
          return targetPercentage;
        }
        return next;
      });
    }, 50);
  }, [displayedPercentange, targetPercentage]);

  const percentageDispayed = useMemo(
    () => displayedPercentange?.toFixed(0),
    [displayedPercentange]
  );

  return (
    <div className="bg-white shadow rounded-lg p-4 flex flex-col items-center space-y-2">
      <div className="text-lg font-semibold">{title}</div>
      <div className="text-2xl font-bold">{percentageDispayed}%</div>
      <div className="text-sm text-gray-600">{fraction}</div>
      <div className="w-full h-2 rounded-full bg-gray-300">
        <div
          className="h-2 rounded-full bg-blue-500 transition-all duration-500"
          style={{ width: `${targetPercentage}%` }}
        ></div>
      </div>
    </div>
  );
};

interface BigNumberProps {
  title: string;
  value: number;
}

export const BigNumber: FC<BigNumberProps> = ({ title, value }) => {
  return (
    <div className="bg-white shadow rounded-lg p-4 flex flex-col items-center space-y-2">
      <p className="text-sm font-semibold">{title}</p>
      <h5 className="text-2xl font-bold">{value}</h5>
    </div>
  );
};
